<template>
    <div class="home">
        <div class="header">
            <div class="address_map" @click="$router.push({name:'Address',params:{city:city}})">
                <i class="fa fa-map-marker"></i>
                <span>{{address}}</span>
                <i class="fa fa-sort-desc"></i>
            </div>
        </div>
        <div class="search_wrap"  :class="{'fixedview':showFilter}" @click="$router.push('/search')">
            <div class="shop_search">
                <i class="fa fa-search"></i>
                搜索商家 商家名称
            </div>
        </div>
        <div id="container" >
            <!-- 轮播 -->
            <mt-swipe :auto="4000" class="swiper">
                <mt-swipe-item v-for="(img,index) in swipeImgs.value" :key="index">
                    <img :src="img" alt="">
                </mt-swipe-item>
            </mt-swipe>
            <!-- 分类 -->
            <mt-swipe :auto="0" class="entries">
                <mt-swipe-item v-for="(entry,i) in entries.value" :key="i" class="entry_wrap">
                    <div class="foodentry" v-for="(item,index) in entry" :key="index">
                        <div class="img_wrap">
                            <img :src="item.image" alt="">
                        </div>
                        <span>{{item.name}}</span>
                    </div>
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <!-- 推荐商家 -->
        <div class="shoplist-title">推荐商家</div>

        <!-- 导航 -->
        <FilterView class='aa' :filterData='filterData.value' @searchFixed='showFilterView' @update='update'/>

        <!-- 商家信息 -->
        <mt-loadmore :auto-fill='false' :bottomPullText="bottomPullText" :top-method="loadData" :bottom-method="loadMore" :bottom-all-loaded="allLoaded" ref="loadmore">
            <div class="shoplist">
                <IndexShop v-for="(item,index) in restaurants" :key="index" :restaurant='item.restaurant'/>
            </div>
        </mt-loadmore>
    </div>
</template>

<script>
    import {
        computed,
        onMounted,
        reactive,
        toRefs,ref,watch
    } from '@vue/composition-api'
    import axios from 'axios'
    import { Swipe, SwipeItem,Loadmore } from 'mint-ui';
    import FilterView from '../components/FilterView'
    import IndexShop from '../components/IndexShop'
    export default {
        name: 'Home',
        components:{
            FilterView,
            IndexShop
        },
        setup(props, {
            root,refs
        }) {
            const restaurants=ref([]) //存放所有商家的商品
            const data=reactive({
                swipeImgs:[],
                entries: [],
                filterData:{},
                page:1,
                size:5,
                
                allLoaded:false,
                bottomPullText:'上拉加载更多',
                dataa:null
            })
            const showFilter=ref(false)
            const address = computed(() => {
                return root.$store.getters.address
            })

            const city = computed(() => {
                return root.$store.getters.location.addressComponent.city || root.$store.getters.location
                    .addressComponent
                    .province
            })

            const getData=()=>{
                axios.get('/api/profile/shopping').then(res=>{
                    // console.log(res.data);
                    data.swipeImgs.value =res.data.swipeImgs
                    data.entries.value =res.data.entries
                })
                axios.get('/api/profile/filter').then(res=>{
                    // console.log(res.data);
                    data.filterData.value =res.data
                    // console.log(data.filterData.value);
                    
                })
                loadData()
            }
            const showFilterView=(isShow)=> {
                // console.log(showFilter.value);
                showFilter.value =isShow
            }

            const update=(condition)=> {
                // console.log(condition);
                data.dataa=condition
                loadData()
            }
            onMounted(() => {
                getData()
                // console.log(restaurants);
            })
            watch(()=>restaurants.value,(value)=>{
                // console.log(value);
                restaurants.value=value
            })
            const loadData=()=>{
                data.page =1;
                data.allLoaded =false;
                data.bottomPullText ='上拉加载更多'
                //拉取商家信息
                axios.post(`/api/profile/restaurants/${data.page}/${data.size}`,data.dataa).then(res=>{
                    // console.log(res.data);
                    // refs.loadmore.onTopLoaded()
                    restaurants.value =res.data
                    // console.log(data.restaurants);
                })
            }
            
            const loadMore=()=>{
                if(!data.allLoaded){
                    data.page++;
                }
                //拉取商家信息
                axios.post(`/api/profile/restaurants/${data.page}/${data.size}`).then(res=>{
                    //加载完之后重新渲染
                    refs.loadmore.onBottomLoaded()
                    if(res.data.length>0){
                        res.data.forEach(item=>{
                           restaurants.value.push(item)
                            // console.log(data.restaurants);
                        })
                        if(res.data<data.size){
                            data.allLoaded=true;
                            data.bottomPullText='没有更多了哦'
                        }
                    }else{
                        //数据为空
                        data.allLoaded=true;
                        data.bottomPullText='没有更多了哦'
                    }
                })
            }
            return {
                address,
                city,
                ...data,
                showFilterView,
                showFilter,
                update,
                loadData,
                loadMore,
                restaurants
            }
        }
    }
</script>

<style scoped>
    .home {
        width: 100%;
        height: 100%;
        /* overflow: auto; */
        box-sizing: border-box;
    }

    .header,
    .search_wrap {
        background-color: #009eef;
        padding: 10px 16px;
    }

    .header .address_map {
        color: #fff;
        font-weight: bold;
    }

    .address_map i {
        margin: 0 3px;
        font-size: 18px;
    }

    .address_map span {
        display: inline-block;
        width: 80%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .search_wrap .shop_search {
        /* margin-top: 10px; */
        background-color: #fff;
        padding: 10px 0;
        border-radius: 4px;
        text-align: center;
        color: #aaa;
    }

    .search_wrap {
        /* position: -webkit-sticky; */
		position: sticky;
        top: 0;
        z-index: 999;
        box-sizing: border-box;
    }
    .aa{
        position: sticky;
        top: 54px;
        z-index: 999;
        box-sizing: border-box;
    }

    .swiper {
        height: 100px;
    }

    .swiper img {
        width: 100%;
        height: 100px;
    }

    .entries {
        background: #fff;
        height: 47.2vw;
        text-align: center;
        overflow: hidden;
    }

    .foodentry {
        width: 20%;
        float: left;
        position: relative;
        margin-top: 2.933333vw;
    }

    .foodentry .img_wrap {
        position: relative;
        display: inline-block;
        width: 12vw;
        height: 12vw;
    }

    .img_wrap img {
        width: 100%;
        height: 100%;
    }

    .foodentry span {
        display: block;
        color: #666;
        font-size: 0.32rem;
    }

    /* 推荐商家 */
    .shoplist-title {
        display: flex;
        align-items: flex;
        justify-content: center;
        height: 9.6vw;
        line-height: 9.6vw;
        font-size: 16px;
        color: #333;
        background: #fff;
    }

    .shoplist-title:after,
    .shoplist-title:before {
        display: block;
        content: "一";
        width: 5.333333vw;
        height: 0.266667vw;
        color: #999;
    }

    .shoplist-title:before {
        margin-right: 3.466667vw;
    }

    .shoplist-title:after {
        margin-left: 3.466667vw;
    }

    .fixedview {
        width: 100%;
        position: fixed;
        top: 0px;
        z-index: 999;
    }

    .mint-loadmore {
        height: calc(100% - 95px);
        overflow: auto;
    }
</style>